import {Fighter} from './Fighter';

/**
 * This is a class that extends another class
 */
class Pokemon extends Fighter{
    /**
     * Es el constructor de la clase, tiene los mismos parametros que su clase padre, pero la extiende con el parametro tipo
     * @param nombre es el nombre del pokemon
     * @param peso es el peso del pokemon
     * @param altura la altura del pokemon
     * @param tipo tipo de pokemon
     * @param ataque ataque del pokemon
     * @param defensa defensa de el pokemon
     * @param hp vida del pokemon
     * @param velocidad velocidad del pokemon
     */
    constructor(protected readonly nombre: string, protected peso: number, protected altura: number, protected tipo: string, protected ataque: number, protected defensa: number, protected hp: number, protected velocidad: number) {
        super(nombre, peso, altura, ataque, defensa, hp, velocidad);
    }
    /**
     * Es el getter del atributo nuevo que a√±ade esta clase
     * @return retorna el tipo del pokemon
     */
    getTipo(): string {
        return this.tipo;
    }
    /**
     * Es el metodo abastracto que tienen que implementar las clases derivadas de Figther
     * @return retorna el grito de ataque del pokmon
     */
    gritoDeAtaque() {
        return "Pokemon " + this.nombre + " lanzando ataque!!";
    }
}

export {Pokemon};